.title {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 4rem;
  h2 {
    text-transform: uppercase;
    position: absolute;
    z-index:0;
    color: rgba(240, 255, 255, 0.05);
    font-size: 8rem;
    word-spacing: 1rem;
    letter-spacing: .5rem;
  }
  h3 {
    text-transform: capitalize;

    font-size: 2.7rem;
  }
  .hr{
    width: 10%;
    border-top: 4px solid #9a0680 ;

  }
}
